<template>
  <section class="header">
    <div class="head-title">厚载人生 - 个人博客</div>
    <div class="head-menu">
      <div class="menu-item" v-for="(item,index) in headMenuList" :key="index">
        <div :class="`menu-item-text ${item.key == activeKey?'active':''}`" @click="menuClick(item)">{{item.name}}</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      headMenuList:[
        {name:'首页',path:"/index",key:'home'},
        {name:'编辑',path:"/modifyPage",key:'modifyPage'},
      ],
      activeKey:'',
    }
  },
  mounted(){
    this.activeKey=this.$route.name;
  },
  methods:{
    menuClick(item){
      this.activeKey = item.key;
      this.$router.push(item.path);
    }
  }
}
</script>

<style scoped lang="scss">
  .header{
    display: flex;
    place-content: center;
    place-items: center;
    width: 100%;
    height: 70px;
    background: #ffffff;
    box-shadow: 0 2px 6px 0 rgba(0,0,0,.05);
    .head-title{
      width: 200px;
      padding-right: 20px;
      margin-left: -200px;
      font-size: 18px;
      font-weight: 600;
      box-sizing: border-box;
    }
    .head-menu{
      display: flex;
      width: 30%;
      height: 100%;
      place-items: center;
      .menu-item{
        height: 100%;
        padding: 0 20px;
        .menu-item-text{
          position: relative;
          padding: 25px 20px;
          color: #666;
          cursor: pointer;
          transition: all linear 300ms;
        }
        .menu-item-text:hover{
          color: #111;
          background: #eee;
        }
        .menu-item-text.active{
          color: #111;
          font-weight: 600;
          &::after{
            content: "";
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
            height: 4px;
            background: #21b351;
          }
        }
      }
    }
  }
</style>



