<template>
  <section class="home">
    <Header></Header>
    <div class="content">
      <router-view></router-view>
    </div>
  </section>
</template>

<script>
  import textMarkDown from '@/assets/js/context.js'
  import Header from '../components/Header'
export default {
  name: 'index',
  components:{
    Header
  },
  data () {
    return {
      textMarkDown,
      Inde:0
    }
  },
  methods:{
    pre(){
      if(this.Inde == 0)return;
      this.Inde--;
    },
    next(){
      if(this.Inde == this.textMarkDown.length-1)return;
      this.Inde++;
    },
  }
}
</script>

<style scoped lang="scss">
  .home{
    width: 100%;
    height: 100%;
    .content{
      width: 100%;
      height: calc(100% - 70px);
    }
  }
</style>



